import { Component, OnInit } from '@angular/core';
import { VendorService } from '../vendor.service';
import { Vendor } from './vendor';
import { Router } from '@angular/router';

@Component({
  selector: 'app-vendor',
  templateUrl: './vendor.component.html',
  styleUrls: ['./vendor.component.scss']
})
export class VendorComponent implements OnInit {

vendors:Vendor[];
serachText;
searchid:any;
view:any;
  constructor(private service:VendorService,private router:Router) { }

  ngOnInit() {
    this.getAllVendors();
  }

  addVendor(){
    this.router.navigate(['addVendor'])
  }

  editVendor(data){
    this.router.navigate(['editVendor',data])
  }

  getAllVendors(): void{
    this.service.getAllVendors()
    .subscribe((vendor)=>{
      this.vendors=vendor
    }, (error)=>{
      console.log(error);
    });
  }

  viewContact(data){
    this.router.navigate(['viewcontact',this.view])
  }


  disableVendor()
  {
    console.log("inside disable" +this.searchid);
    this.service.disablevendor(this.searchid)
    .subscribe((data)=>{
      console.log(data);
      this.getAllVendors();
    },(error)=>{
      console.log(error);
    });   
  }
  onSelect(selectedVendor: any) {
    console.log("Selected item Id:", selectedVendor.vendor_id);
    this.searchid = selectedVendor.vendor_id;
  }
  onSelect1(selectedVendor: any) {
    console.log("Selected item Id:", selectedVendor.vendor_id);
    this.view = selectedVendor.vendor_id;
    this.viewContact;
    this.viewContact(this.view);
  }

}
