import { Component, OnInit } from '@angular/core';
import { VendorService } from 'src/app/vendor.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Vendor } from '../vendor';

@Component({
  selector: 'app-edit-vendor',
  templateUrl: './edit-vendor.component.html',
  styleUrls: ['./edit-vendor.component.scss']
})
export class EditVendorComponent implements OnInit {

  constructor(private _service:VendorService,private route:ActivatedRoute,private router:Router) { }

  ngOnInit() {
    this.getVendorById();
    this.resetform();
  }

  vId:any;
  vendorName:any;
  vendor = new Vendor();
  // vendor:Vendor;

  resetform() {
    this._service.vendor = {
      vendor_id: null,
      vendor_name: '',
      address: '',
      location: '',
      service: '',
      pincode: '',
      cont_id: '',
      name: '',
      dept: '',
      email: '',
      phone: '',
      isActive: null
    }
  }

  getVendorById(){
    this.resetform();
    this.vId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vId)
    .subscribe((data) =>{
      this.vendor=data;
      console.log('inside');
      console.log(data);
      
      console.log(this.vendor.vendor_name);
    },(error)=>{
      console.log(error);
    })
  }

  saveVendor() {
    this.resetform();
    this._service.updateVendor(this.vendor,this.vId)
    .subscribe((response)=>{
      
      console.log(response);
      // this.toastr.success('Saved successfully');
      this.router.navigate(['vendor'])
    }, (error)=>{
      console.log(error);
    });
  }

}
