import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './Login/login/login.component';
import { VendorComponent } from './vendor/vendor.component';
import { AddVendorComponent } from './vendor/add-vendor/add-vendor.component';
import { EditVendorComponent } from './vendor/edit-vendor/edit-vendor.component';
import { ViewContactComponent } from './vendor/view-contact/view-contact.component';


const routes: Routes = [
  {
    path:'',
    component:LoginComponent
  },
  {
    path:'vendor',
    component:VendorComponent
  },
  {
    path:'addVendor',
    component:AddVendorComponent
  },
  {
    path:'editVendor/:id',
    component:EditVendorComponent
  },
  {
    path:'viewcontact/:id',
    component:ViewContactComponent
  }


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
